.container {
    display: flex;
    width: 100%;
    flex-flow: column;
    padding-top: 100rem;
    height: fit-content;
    min-height: 100vh;
    flex: 1 0 auto;
}

.header {
    font-size: 2em;
    font-weight: bold;
    width: 100%;
    padding: 20rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex: 0 0 auto;
}

.content {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: max-content;
    padding: 0 50rem 50rem 50rem;
    gap: 50rem;
    flex: 1;
}

.image {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    max-width: 50%;
    height: 100%;
    aspect-ratio: 1/1;
    overflow: hidden;
}

.image img {
    position: absolute;
    border-radius: 50rem;
    object-fit:cover;
    max-width: 90%;
    max-height: 90%;
    /* width: clamp(fit-content, fit-content, 100%); */
    /* height: clamp(fit-content, fit-content, 100%);  */
}

.text {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    max-width: 50%;
    min-width: 50%;
    flex: 1;
    text-wrap: pretty;
    text-wrap: wrap;
}

@media (max-width: 800px) {
    .container {
        padding-top: 60rem;
    }
    .content {
      flex-flow: column;
      justify-content: flex-start;
      align-items: center;
      gap: 20rem;
    }
    .image {
        align-items: center;
        justify-content: center;
        max-height: 50%;
        max-width: 100%;
        height: 50%;
        width: fit-content;
        min-height: 50%;
        min-width: fit-content;
        padding-top: 0;
        padding-bottom: 0;
    }
    .text {
        width: 100%;
        max-width: 100%;
        flex: 0 1 auto;
        width: 100%;
        height: fit-content;
    }
  }